import Vue from 'vue';
import Router from 'vue-router';
import TestPage from '../components/testPage.vue';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/testPage',
            component: TestPage
        },
        {
            path: '/docs/button',
            name: 'button',
            component: r => require.ensure([], () => r(require('../docs/button.md')))
        }
    ],
});


